@import url(https://fonts.googleapis.com/css2?family=Rampart+One&family=Gruppo&family=Press+Start+2P&family=Quantico&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{background-color:#ff6347;max-height:100vh}.App{text-align:center}.App .App-header{margin:auto;margin-top:50px;font-size:calc(10px + 2vmin);color:white}.App .App-header h1{font-family:"Press Start 2P", cursive;font-size:2em;color:#4db6ac;text-shadow:1px 1px 0 #fff}.App .App-header .dual-container{display:flex;justify-content:space-around;max-width:40vw;min-width:320px;margin:auto;font-family:"Gruppo",cursive;border:4px solid #ff6347;box-shadow:1px 1px 2px 2px #b2dfdb;border-radius:20px;font-size:1em}.App .App-header .dual-container #break-label,.App .App-header .dual-container #session-label{font-size:1.5em;color:#4db6ac;text-shadow:1px 1px 0 #fff}.App .App-header .dual-container .time-sets{display:flex;justify-content:space-evenly;align-items:center}.App .App-header .dual-container .time-sets button{border-radius:10px;color:#fff;border:2px solid #ff6347;display:flex;justify-content:center;align-items:center}.App .App-header .dual-container .time-sets button:hover{color:#ff8c00;border:2px solid #fff}.App .App-header .dual-container .time-sets #break-length,.App .App-header .dual-container .time-sets #session-length{font-size:1.4em;font-family:"Gruppo",cursive;font-weight:bold;color:#4db6ac;text-shadow:1px 1px 0 #fff;padding-bottom:15px}.App .App-header .display{font-family:"Gruppo",cursive}.App .App-header .display #timer-label{font-size:3em;color:#ff6347;text-shadow:2px 2px 0 #b2dfdb}.App .App-header .display #time-left{font-family:"Quantico", sans-serif;font-size:2em;color:#b2dfdb;text-shadow:2px 2px 0 #ff4500}.App .App-header .display-buttons{display:flex;justify-content:center}.App .App-header .display-buttons button{border-radius:10px;color:#fff;border:2px solid #ff6347;display:flex;justify-content:center;align-items:center;width:80px;height:40px;margin:10px}.App .App-header .display-buttons button:hover{color:#ff8c00;border:2px solid #fff}.App #da3ker{color:white;font-size:0.9em;letter-spacing:2px;font-family:"Rampart One", cursive;margin-top:1em}

